import React from 'react'
import gif from './img/TFA-LOGO-VIDEO.gif';
import AOS from 'aos';
import "aos/dist/aos.css";

const Hero = () => {
  AOS.init();
    return (
        <section id="hero" className="d-flex align-items-center">

    <div className="container">
      <div className="row gy-4">
       
        <div className="col-lg-6 order-1 order-lg-1 hero-img">
          <img src={gif} className="img-fluid animated" alt="" />
          <h1>Recreate Learning, with an affordable price!</h1>
          <h2>Missed your concepts? No worries, TechnologyForAll is here to help. Now unlock the plethora of concepts blended with conceptual and practical implementation topic by topic. We are there for you with every step you take!
          </h2>
          <div>
            <a href="#about" className="btn-get-started scrollto">Get Started</a>
            <span style={{ marginRight: '20px' }}></span>
            <a href="#about" className="btn-get-started scrollto aboutus">AboutUs</a>
          </div>
        </div>
        <div className="col-lg-6 order-2 order-lg-2 d-flex flex-column justify-content-center" style={{alignItems: "center"}}>
          <div className="contact-info">
            <h3>Gear-up for a better future with
              Technology for all</h3>
              <form action="/action_page.php">
                <input type="text" id="fname" name="firstname" placeholder="Your name.." />
                <input type="text" id="lname" name="lastname" placeholder="Your Number.."/>
                <input type="text" id="lname" name="lastname" placeholder="Your email.." />
                <select id="country" name="country">
                   <i className="fa fa-sort-desc" aria-hidden="true" />
                  <option value="australia">Select Course Name</option>
                  <option value="canada">Data Science</option>
                  <option value="usa">Full stack Development</option>
                </select>
                <input type="submit" value="Register Now" />
              </form>
            </div>
        </div>
      </div>
    </div>

  </section>
    )
}

export default Hero
