import React from 'react'
import backgroundtfa from './img/why-tfa-bg.png'
import AOS from 'aos';
import "aos/dist/aos.css";
const Whytfa = () => {
  AOS.init();
    return (
        <section id="whytfa" className="why-tfa section-bg" style={{backgroundImage: "url("+ backgroundtfa +")"}}>
        <div className="container" data-aos="fade-up">
  
          <div className="section-title">
            
            <p>Why Technology for All?</p>
            <span style={{color: "#cccccc"}}>Making learning accessible and affordable for individuals and so <br/>they gain vast knowledge in every â€œCutting-Edge Technologyâ€
            </span>
          </div>
  
          <div className="container">
            <div className="row justify-content-center text-center">
              <div className="col-md-3">
              <span id="count1" className="display-4"> </span><b className="signs"> + </b>
              <p style={{fontSize: "23px"}}>Students Trained</p>
             </div>
             <div className="col-md-3">
              <span id="count2" className="display-4"></span><b className="signs"> % </b>
              <p style={{fontSize: "23px"}}>Job Assistance</p>
             </div>
             <div className="col-md-3">
              <span id="count3" className="display-4"></span><b className="signs"> + </b>
              <p style={{fontSize: "23px"}}>Hiring partners</p>
             </div>
             <div className="col-md-3">
              <span id="count4" className="display-4"></span><b className="signs"> + </b>
              <p style={{fontSize: "23px"}}>Industry Experet</p>
             </div>
            </div>
           </div>
  
        </div>
      </section>
    )
}
document.addEventListener("DOMContentLoaded", () => {
  function counter(id, start, end, duration) {
   let obj = document.getElementById(id),
    current = start,
    range = end - start,
    increment = end > start ? 1 : -1,
    step = Math.abs(Math.floor(duration / range)),
    timer = setInterval(() => {
     current += increment;
     obj.textContent = current;
     if (current == end) {
      clearInterval(timer);
     }
    }, step);
  }
  counter("count1", 0, 200, 3000);
  counter("count2", 50, 100, 2500);
  counter("count3", 0, 20, 3000);
  counter("count4", 0, 10, 2500);
 });


export default Whytfa
