import React from 'react'
import AOS from 'aos';
import "aos/dist/aos.css";

const Review = () => {
  AOS.init();
    return (
        <section id="review" className="review">
  <div className="container" data-aos="fade-up">
    <div className="section-title">
      <p>Happy Clients Say</p>
    </div>
    <div className="row justify-content-between">
      <div className="col-lg-3 d-flex align-items-center justify-content-center">
       <p style={{ width: "50px", background: "#474747", padding: "42px", borderRadius: "45px"}}></p>
      </div>
      <div className="col-lg-9 pt-5 pt-lg-0">
        <div className="row">
          <div className="col-md-12 item-1" data-aos="fade-up" data-aos-delay="100">
            <p>Etiam eget libero sit amet elit aliquet imperdiet sed id velit. Morbi at elit non risus rutrum interdum. Cras vitae neque non lorem pellentesque facilisis. Risus get vulputate porttitor, tortor turpis porta justo.</p>
            <div className="">
                <b>Matilda Joyce</b><br/>
                <span>Jr. Data scientist</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</section>
    )
}

export default Review
