import React from 'react'
import png1 from './img/servicse-img.png';
import services1 from './img/servises1.png';
import services2 from './img/servises2.png';
import services3 from './img/servises3.png';
import icon1 from './img/datascience-icon.png';
import icon2 from './img/fsd-icon.png';
import icon3 from './img/aws-cion.png';
import AOS from 'aos';
import "aos/dist/aos.css";
const Services = () => {
  AOS.init();
    return (
        <section id="services" className="services section-bg">
      <div className="container" data-aos="fade-up">

        <div className="section-title">
          
          <p>Our Services</p>
          <span>Our methodology is quite simple! Learn, Practice, Build and <br/>Produce. Learn the concepts, Practice and apply the concepts.
          </span>
        </div>

        <div className="row">
          <div className="col-md-6 col-lg-4 d-flex align-items-stretch" data-aos="zoom-in" data-aos-delay="100">
            <div className="icon-box">
              <div className="rate-tag">
                <img src={png1}/>
                <p>Only<br/>
                  Rs 8999/-<br/>
                  +GST</p>
              </div>
              <div>
                <img src={services1}/>
              </div>
              <div className="icon-content">
              <div className="icon"><img src={icon1} style= {{width: '50px'}}/></div>
              <div><h4 className="title"><a href="">Data Science</a></h4>
              <p className="description">Data rules the world now! Data Science helps to analyze and predict the data.</p></div>
              </div>
            </div>
          </div>

          <div className="col-md-6 col-lg-4 d-flex align-items-stretch" data-aos="zoom-in" data-aos-delay="200">
            <div className="icon-box">
              <div className="rate-tag">
                <img src={png1}/>
                <p>Only<br/>
                  Rs 8999/-<br/>
                  +GST</p>
              </div>
              <div>
                <img src={services2}/>
              </div>
              <div className="icon-content">
              <div className="icon"><img src={icon2} style={{width: "50px"}}/></div>
              <div><h4 className="title"><a href="">Full Stack Development</a></h4>
              <p className="description">Create cool web apps like Netflix and YouTube and scale up your web skills.</p></div>
            </div>
            </div>
          </div>

          <div className="col-md-6 col-lg-4 d-flex align-items-stretch" data-aos="zoom-in" data-aos-delay="300">
            <div className="icon-box">
              <div className="rate-tag">
                <img src={png1}/>
                <p>Only<br/>
                  Rs 8999/-<br/>
                  +GST</p>
              </div>
              <div>
                <img src={services3}/>
              </div>
              <div className="icon-content">
              <div className="icon"><img src={icon3} style={{width: "50px"}}/></div>
              <div><h4 className="title"><a href="">Amazon Web Services</a></h4>
              <p className="description">Learn advanced server concepts and excel in edge-cutting server skills.</p></div>
            </div>
          </div>
        </div>
        </div>
      </div>
    </section>
    )
}

export default Services
