import React from 'react'
import logo from './img/Technology-For-All-Logo.svg';
import AOS from 'aos';
import "aos/dist/aos.css";
const Navbar = () => {
  AOS.init();
    return (
        <header id="header" className="fixed-top d-flex align-items-center">
    <div className="container d-flex align-items-center justify-content-between">

      <div className="logo">
        <h1 className="text-light"><a href="https://www.technologyforall.in/"><span><img src={logo}/></span></a></h1>
      </div>

      <nav id="navbar" className="navbar">
        <ul>
          <li><a className="nav-link scrollto active" href="#hero">Home</a></li>
          <li className="dropdown"><a href="#"><span>Courses</span> <i className="bi bi-chevron-down"></i></a>
            <ul>
              
              <li className="dropdown"><a href="#"><span>Data Science</span> <i className="bi bi-chevron-right"></i></a>
                <ul>
                  <li><a href="#">Data Science</a></li>
                  <li><a href="#">Data Science</a></li>
                  <li><a href="#">Data Science</a></li>
                  <li><a href="#">Data Science</a></li>
                  <li><a href="#">Data Science</a></li>
                </ul>
              </li>
              <li><a href="#">Full stack Development</a></li>
              <li><a href="#">Digital Marketing</a></li>
              <li><a href="#">AWS</a></li>
            </ul>
          </li>
          <li><a className="nav-link scrollto" href="#about">About Us</a></li>
          <li><a className="nav-link scrollto" href="#services">Reach Us</a></li>
          <li><a className="nav-link scrollto" href="#contact">Contact</a></li>
          <li><a className="getstarted scrollto" href="#about">Get Started</a></li>
        </ul>
        <i className="bi bi-list mobile-nav-toggle"></i>
      </nav>

    </div>
  </header>
    )
}

export default Navbar
