import React from 'react'
import AOS from 'aos';
import "aos/dist/aos.css";
const Contactus = () => {
  AOS.init();
    return (
        <section id="contact" className="contact" style={{backgroundImage: "linear-gradient(to right, #053a60, #110557)"}}>
      <div className="container" data-aos="fade-up">

        <div className="section-title">
         
          <p style={{color: "white"}}>Contact us to get started</p>
        </div>

        <div className="row">

          <div className="col-lg-5 d-flex align-items-stretch" data-aos="fade-up" data-aos-delay="100">
            <div className="info">
              <h2 style={{color: "white"}}>Info</h2>
            <p>Praesent interdum congue mauris, et fringilla lacus pel
              vitae. Quisque nisl mauris, aliquam eu ultrices vel, conse
              vitae sapien at imperdiet risus. Quisque cursus risus id.
              fermentum, in auctor quam consectetur.</p>
              <p>Address:  Plot no 21, 2nd phase, JPN Nagar,Miyapur,
                Hyderabad Telangana 500048
                <br/>
                Phone:  +91-6304655320<br/>
                Email:  info@technologyforall.in<br/>
                </p>
                <div className="social-link mt-3">
                  <a href="#" className="twitter"><i className="fa fa-twitter"></i></a>
                  <a href="#" className="facebook"><i className="fa fa-facebook"></i></a>
                  <a href="#" className="instagram"><i className="fa fa-instagram"></i></a>
                  <a href="#" className="google-plus"><i className="fa fa-skype"></i></a>
                  <a href="#" className="linkedin"><i className="fa fa-linkedin"></i></a>
                </div>
            </div>

          </div>

          <div className="col-lg-7 mt-5 mt-lg-0 d-flex align-items-stretch" data-aos="fade-up" data-aos-delay="200">
            <form action="forms/contact.php" method="post" role="form" className="php-email-form">
                <div className="form-group">
                
                  <input type="text" name="name" className="form-control" id="name" placeholder="Your Name" required/>
                </div>
                <div className="form-group mt-3 mt-md-0">

                  <input type="email" className="form-control" name="email" id="email" placeholder="Your Email" required style={{margin : '6px 0px 16px'}}/>
                </div>
              <div className="form-group mt-3">
               
                <input type="text" className="form-control" name="subject" id="subject" placeholder="Your Title" required/>
              </div>
              <div className="form-group mt-3">
                
                <textarea className="form-control" name="message" rows="10" placeholder="Message" required></textarea>
              </div>
              <div className="my-3">
                <div className="loading">Loading</div>
                <div className="error-message"></div>
                <div className="sent-message">Your message has been sent. Thank you!</div>
              </div>
              <div className=""><button type="submit">Send Message</button></div>
            </form>
          </div>

        </div>

      </div>
     
    </section>
    )
}

export default Contactus
